@import url(https://fonts.googleapis.com/css2?family=Lato:ital@0;1&display=swap);
body {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
  height: 100%;
  color: rgb(26, 130, 221);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Lato", "Segoe UI", "Castoro",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bottombar {
  color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  min-height: 55px;
  height: 7vh;
  background-color: rgb(255, 234, 250);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icons {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}

.bottombar a {
  color: rgb(5, 122, 255);
  height: 50px;
}

.bottombar a:hover {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  box-shadow: 0px 0px 20px -5px violet;
}

.toTheLeft {
  margin-left: 0.2vw;
}

img {
  margin-left: 1vw;
  margin-right: 1vw;
  margin-bottom: auto;
  height: 5vh;
  min-width: 40px;
  min-height: 40px;
}

.attributions {
  align-self: flex-end;
  text-align: center;
  font-size: 0.6rem;
}

.container-fluid {
  justify-content: center;
  align-items: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: linear-gradient(180deg, rgb(73, 62, 126), rgb(14, 0, 54));
  background-attachment: fixed;
  background-position: center;
}

.vertical {
  border: groove 8px rgba(97, 69, 116, 0.342);
  border-left: none;
  border-right: none;
  border-top: none;
  background-color: rgba(2, 0, 7, 0.459);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 100vw;
  padding-left: 7vw;
  padding-right: 7vw;

  box-shadow: 0px 10px 100px -10px rgb(26, 26, 26);
  text-align: center;
  justify-content: center;
  border-radius: 3px;
}

h3 {
  font-size: 10vw;
  margin-top: 2vh;
  margin-bottom: auto;
  width: 100%;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.836);
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 0.07rem;
  -webkit-text-stroke-color: rgb(0, 0, 0);
  background: linear-gradient(180deg, rgb(0, 255, 157) 0%, rgb(234, 0, 255));
  -webkit-background-clip: text;
          background-clip: text;
}

.topbar {
  top: 0;
  position: absolute;
  height: 7vh;
  width: 100%;
  background-color: rgba(7, 2, 14, 0.863);
  box-shadow: 0px 15px 50px -10px rgb(0, 0, 0);
}

.nametitle {
  -webkit-animation-duration: 1500ms;
          animation-duration: 1500ms;
}

h5 {
  font-size: 2vw;

  font-weight: 500;
  font-style: italic;
  color: rgb(255, 234, 250);
  text-shadow: 0px 0px 7px rgba(15, 15, 15, 0.753);
  background-color: rgba(31, 31, 31, 0.486);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-left: 1vw;
  padding-right: 1vw;
  margin-left: auto;
  margin-right: auto;
  border-radius: 3px;
}

h4 {
  font-size: 8vw;
  margin-top: auto;
  margin-bottom: auto;
  width: 100%;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.836);
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 0.07rem;
  -webkit-text-stroke-color: rgb(0, 0, 0);
  background: linear-gradient(180deg, rgb(0, 255, 157) 0%, rgb(234, 0, 255));
  -webkit-background-clip: text;
          background-clip: text;
}

.spacer {
  position: absolute;
  bottom: 0;
  height: 3vh;
  background-color: black;
}

.no-gutters,
.row {
  margin-right: 0;
  margin-left: 0;
  padding-right: 0;
  padding-left: 0;
}

.titleName {
  margin-top: auto;
  margin-bottom: auto;
  position: relative;
  display: flex;
  justify-content: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 12vh;
  padding-bottom: 12vh;
  background-color: rgba(215, 226, 235, 0.11);
  box-shadow: 0px 25px 100px -10px rgb(26, 26, 26),
    inset 0px 0px 75px -20px rgba(241, 210, 255, 0.644);
  -webkit-animation-name: titleIntro, fadeIn;
          animation-name: titleIntro, fadeIn;
  -webkit-animation-duration: 1500ms;
          animation-duration: 1500ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
}

.title {
  border-top: groove 3vh rgb(24, 20, 43);
  box-shadow: inset 0px 30px 50px -30px rgb(44, 44, 44);
  display: flex;
  justify-content: center;
  margin: auto;
  height: 100vh;
  text-align: center;
}

.mainContent {
  display: flex;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 100vh;
}

.evenodd:nth-child(odd) {
  background-color: rgba(0, 217, 255, 0.26);
}

.aboutMe {
  color: rgb(255, 234, 250);
  position: relative;
  margin-bottom: 5vh;
  width: 100%;
  padding-bottom: 10vh;
  background-color: rgba(194, 215, 247, 0.342);
  box-shadow: 0px 15px 50px -10px rgb(0, 0, 0),
    inset 0px 0px 150px -30px rgba(175, 224, 233, 0.521);
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  -webkit-animation: fadeInUp;
          animation: fadeInUp;
  -webkit-animation-duration: 1200ms;
          animation-duration: 1200ms;
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0;
}

.silly {
  text-shadow: 0px 3px 10px rgb(0, 0, 0);
  font-size: 24px;
  margin-top: 3vh;
  margin-left: auto;
  margin-right: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3px;
  margin-bottom: 1vh;
  background-color: rgba(31, 31, 31, 0.637);
  box-shadow: 0px 0px 15px -5px rgb(0, 0, 0);
}

.aboutmetext {
  font-size: 20px;
  text-shadow: 0px 3px 10px rgb(0, 0, 0);
  margin-left: auto;
  margin-right: auto;
  max-width: 70vw;

  border-radius: 3px;
  background-color: rgba(31, 31, 31, 0.637);
  box-shadow: 0px 0px 15px -5px rgb(0, 0, 0);
}

.blurb {
  margin-left: 5vw;
  margin-right: 5vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: auto;
  margin-bottom: auto;
}

.center {
  margin-top: auto;
  margin-bottom: auto;
}

.headshot {
  margin-left: auto;
  margin-right: auto;
  height: 50vh;
  width: 100%;
  margin-top: auto;
  margin-bottom: auto;
}

.myface {
  margin-left: auto;
  margin-right: auto;
  border-radius: 3px;
  height: 45vh;
  margin-top: 5vh;
  box-shadow: 0px 0px 15px -5px rgb(0, 0, 0);
}

.quote {
  font-size: 20px;
  position: absolute;
  bottom: 0;
  color: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5vh;
  text-shadow: 0px 0px 7px rgba(0, 0, 0, 0.644);
  background-color: rgba(247, 247, 247, 0.562);
  box-shadow: 0px 0px 15px -5px rgb(0, 0, 0);
}

.aboutMeTitle {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.designs {
  margin-top: 2vh;
  opacity: 0;
  -webkit-animation: fadeInUp;
          animation: fadeInUp;
  -webkit-animation-duration: 1400ms;
          animation-duration: 1400ms;
  -webkit-animation-delay: 300ms;
          animation-delay: 300ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.solutions {
  margin-bottom: 3vh;
  opacity: 0;
  -webkit-animation: fadeInUp;
          animation: fadeInUp;
  -webkit-animation-duration: 1400ms;
          animation-duration: 1400ms;
  -webkit-animation-delay: 700ms;
          animation-delay: 700ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.centering {
  display: flex;
  justify-content: center;
}

.contain {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.test {
  box-shadow: 0px 0px 15px -2px rgb(0, 0, 0);
  border-radius: 3px;
  color: rgb(255, 234, 250);
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 85%;
  border: inset 10px rgba(0, 0, 0, 0.432);
  width: 85%;
  background-color: rgba(18, 9, 26, 0.856);
  margin-top: 5vh;
  margin-bottom: 5vh;
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
}

.links {
  justify-content: center;
  display: flex;
}

.links h6 {
  font-size: 18px;
  margin-left: 2vh;
  margin-right: 2vh;
  border-radius: 3px;
}

.links a {
  color: rgb(255, 234, 250);
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 2vh;
  margin-right: 2vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: box-shadow ease-in-out 300ms;
  box-shadow: 0px 0px 12px -2px rgba(255, 255, 255, 0.164);
  background-color: rgba(0, 0, 0, 0.822);
  padding: 1vh;
}

.links a:hover {
  transition: box-shadow ease-in-out 300ms;
  box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.175);
}

.github {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.deployed {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.projecttext {
  opacity: 0;
  font-size: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5vh;
  max-width: 70vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.774);
  background-color: rgba(7, 7, 7, 0.329);
  margin-top: 2vh;
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.projectimg {
  margin-top: 3vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(9, 3, 14, 0.87);
}

.projectimg img {
  height: 30vh;
  max-width: 65vw;
}

.goodbye {
  opacity: 0;
  border: inset 10px rgba(0, 0, 0, 0.432);
  margin-left: auto;
  margin-right: auto;
  border-radius: 3px;
  min-height: 70vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 85%;
  background-color: rgba(103, 169, 231, 0.724);
  display: flex;
  box-shadow: 0px 0px 10px -2px rgb(0, 0, 0);
  justify-content: center;
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.goodbye h3 {
  margin-top: 5vh;
  font-size: 10vw;
}

.morelater {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.goodbyetext {
  color: rgb(0, 0, 0);
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.253);
  background-color: rgba(255, 255, 255, 0.253);
  border-radius: 3px;
  font-size: 20px;
  width: 55vw;
  max-width: 900px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 5vh;
}
.contact {
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.253);
  color: rgb(0, 0, 0);
  background-color: rgba(255, 255, 255, 0.253);
  border-radius: 3px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: auto;
}

h1 {
  font-size: 22px;
  margin-left: 3vw;
  margin-right: 3vw;
}

@-webkit-keyframes titleIntro {
  0% {
    transform: translateY(50%);
  }
}

@keyframes titleIntro {
  0% {
    transform: translateY(50%);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 768px) {
  h3 {
    font-size: 12vw;
  }
  h4 {
    font-size: 10vw;
  }
  h5 {
    font-size: 3vw;
  }
}

